import Member from './components/Member/member'
import Donate from './components/Donate/donate'


class App extends Component {
  render() {

    return (
      <div>
        <Switch>
          <Route path="/hoyipyik/wa2-react/" exact >
            <Home/>
          </Route>
          <Route path="/hoyipyik/wa2-react/playhouse.html">
            <Playhouse/>
          </Route>
          <Route path="/hoyipyik/wa2-react/member.html">
            <Member/>
          </Route>
          <Route path="/hoyipyik/wa2-react/donate.html">
            <Donate/>
          </Route>
        </Switch>
      </div>
    )
  }
}

export default App